<template>
    <div>
        <div class="container">
            <div class="form-box">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="工号">
                        <el-input v-model="form.workId" :disabled="true">{{form.workId}}</el-input>
                    </el-form-item>
                    <el-form-item label="姓名">
                        <el-input v-model="form.fullname" >{{form.fullname}}</el-input>
                    </el-form-item>
                    <el-form-item label="假期类型">
                        <el-select v-model="form.vacationType" placeholder="请选择">
                            <el-option label="病假" value="病假"></el-option>
                            <el-option label="婚假" value="婚假"></el-option>
                            <el-option label="产假" value="产假"></el-option>
                            <el-option label="丧假" value="丧假"></el-option>
                            <el-option label="年假" value="年假"></el-option>
                            <el-option label="事假" value="事假"></el-option>
                            <el-option label="探亲假" value="探亲假"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="开始时间">
                        <el-date-picker
                            v-model="form.startTime"
                            type="date"
                            format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="结束时间">
                        <el-date-picker
                            v-model="form.endTime"
                            type="date"
                            format="yyyy-MM-dd"
                            value-format="yyyy-MM-dd"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="部门岗位">
                        <el-select v-model="form.office" placeholder="请选择">
                            <el-option key="002" label="行政岗位" value="001"></el-option>
                            <el-option key="007" label="人事部门员工" value="002"></el-option>
                            <el-option key="008" label="销售部门员工" value="003"></el-option>
                            <el-option key="009" label="财务部门员工" value="004"></el-option>
                            <el-option key="010" label="美术部门员工" value="005"></el-option>
                            <el-option key="011" label="策划部门员工" value="006"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="请假原因">
                        <el-input type="textarea" rows="5" v-model="form.reason"></el-input>
                    </el-form-item>
                    <el-form-item label="申请附件">
                        <el-upload
                            class="upload-demo"
                            action="#"
                            multiple
                            :on-change="handleChange"
                            :on-remove="handleRemove"
                            :file-list="fileList"
                            :auto-upload="false">
                            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                        </el-upload>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">表单提交</el-button>
                        <el-button>取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'vacationApply',
    data() {
        return {
            form: {
                workId: this.$store.state.user.workId,
                office: '',
                fullname: this.$store.state.user.fullname,
                vacationType: '',
                reason: '',
                startTime: '',
                endTime: '',
                file:''

            },
            fileList: []
        };
    },
    methods: {
        onSubmit() {
            let formData = new FormData()
            this.fileList.forEach(file => {
                formData.append('file', file.raw)
            })
            formData.append("vacation", JSON.stringify(this.form))
            this.fileRequest('oasys-back-end/vacationAdd',formData).then(resp=>{
                console.log(resp)
                if (resp.data===true){
                    this.$message.success('提交成功！');
                    this.$router.replace("vacationList")
                }else {
                    this.$message.error("提交失败")
                }
            })


        },
        onFileSubmit(){
            this.fileRequest('oasys-back-end/resignApply',this.form.applyDoc).then(resp=>{
                if (resp){
                    this.$message.success("上传成功")
                }else {
                    this.$message.error("上传失败")
                }
            })
        },
        handlePreview(file){
            console.log("file="+file)
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handleChange(file, fileList) {
            this.fileList = fileList
            console.log(fileList)
        },
    }
};
</script>



