<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 基础表格
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-input v-model="keyword" placeholder="请输入关键字" clearable prefix-icon="el-icon-search"
                          class="handle-input"></el-input>
                <el-button type="primary" @click="getData()">搜索</el-button>
            </div>
            <el-table
                :data="tableData"
                border
                class="table"
                ref="multipleTable"
                header-cell-class-name="table-header"
                @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column prop="workId" label="工号" align="center"></el-table-column>
                <el-table-column label="全名" align="center">
                    <template slot-scope="scope">{{ scope.row.employee.fullname }}</template>
                </el-table-column>
                <el-table-column label="起始日期" align="center" prop="startTime"></el-table-column>
                <el-table-column label="结束日期" align="center" prop="endTime"></el-table-column>
                <el-table-column label="请假类型" align="center" prop="vacationType"></el-table-column>
                <el-table-column label="状态" align="center">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.status==='未审核'?'warn':(scope.row.status==='通过'?'success':(scope.row.status==='未通过'?'danger':'warn'))"
                        >{{ scope.row.status }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="handleDetail(scope.$index, scope.row)"
                        >详细信息
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="handleEdit(scope.$index, scope.row)"
                        >审核
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    background
                    layout="prev, pager, next ,->, total"
                    :page-count="pageTotal"
                    :current-page="query.pageIndex"
                    :page-size="query.size"
                    @current-change="handlePageChange"
                    @size-change="sizeChange"
                    @prev-click="handlePageChange"
                    @next-click="handlePageChange"
                ></el-pagination>
            </div>
        </div>

        <!-- 详细情况弹出框 -->
        <el-dialog title="编辑" :visible.sync="detailVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="工号">
                    <el-input v-model="form.workId" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="全名">
                    <el-input v-model="form.employee.fullname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="请假类型">
                    <el-input v-model="form.vacationType" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="部门岗位">
                    <el-input v-model="form.office" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="请假原因">
                    <el-input type="textarea" v-model="form.reason" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="开始时间">
                    <el-input v-model="form.startTime" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="结束时间">
                    <el-input v-model="form.endTime" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="申请材料">
                    <el-input v-model="form.applyDoc" :disabled="true"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="success" @click="detailVisible=false">关闭</el-button>
            </span>
        </el-dialog>

        <!-- 详细情况弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="工号">
                    <el-input v-model="form.workId" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="全名">
                    <el-input v-model="form.employee.fullname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="请假类型">
                    <el-input v-model="form.vacationType" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="部门岗位">
                    <el-input v-model="form.office" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="请假原因">
                    <el-input type="textarea" v-model="form.reason" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="开始时间">
                    <el-input v-model="form.startTime" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="结束时间">
                    <el-input v-model="form.endTime" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="申请材料">
                    <el-input v-model="form.applyDoc" :disabled="true"></el-input>
                </el-form-item>
            </el-form>
            <el-form ref="checkForm" :model="checkForm" label-width="70px">
                <el-form-item label="审核意见">
                    <el-input type="textarea" v-model="checkForm.checkReason" ></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="danger" @click="saveEdit('未通过')">未通过</el-button>
                <el-button type="success" @click="saveEdit('通过')">通 过</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {getRequest} from "../../util/api";

export default {
    name: "vacationCheck",
    data() {
        return {
            query: {
                address: '',
                name: '',
                pageIndex: 1,
                size: 0
            },
            keyword: null,
            tableData: [],
            multipleSelection: [],
            delList: [],
            editVisible: false,
            detailVisible: false,
            addVisible: false,
            pageTotal: 0,
            total: 0,
            size: 5,
            page: 1,
            form: {
                workId: "",
                vacationType: '',
                office: "",
                status: "",
                reason: "",
                startTime: "",
                endTime: "",
                applyDoc: "",
                checkId: "",
                employee:{
                    fullname: ""
                }
            },
            checkForm:{
                checkId: "",
                workId: this.$store.state.user.workId,
                checkReason: ''
            },
            idx: -1,
            id: -1,
        };
    },
    mounted() {
        this.getData();
    },
    methods: {
        getData() {
            //console.log(this.$store.state.user.roleId)
            let url = 'oasys-back-end/getVacation?page=' + this.page + '&size=' + this.size + '&roleId=' + this.$store.state.user.roleId;
            if (this.keyword != null) {
                url += '&keyword=' + this.keyword
            }
            getRequest(url).then(resp => {
                if (resp!=null){
                    this.tableData = resp.data.data
                    console.log(this.tableData)
                }

                console.log(this.tableData)
                this.total = resp.data.total;
                this.pageTotal = Math.ceil(this.total / this.size)
            })
        },
        // 编辑操作
        handleEdit(index, row) {
            this.idx = index;
            this.form = row;
            this.editVisible = true;
        },
        // 详细信息显示
        handleDetail(index, row) {
            this.idx = index;
            this.form = row;
            this.detailVisible = true;
        },
        // 保存编辑
        saveEdit(params) {
            this.form.status = params
            if (this.form.status === '通过'){
                let checkId = "";
                for (var i=0;i<16;i++){
                    checkId+=Math.floor(Math.random()*10)
                }
                this.checkForm.checkId = checkId;
                this.form.checkId = checkId;
                this.postRequest('/addCheck',this.checkForm).then(resp=>{
                    if (resp.data==true){
                        this.postRequest('oasys-back-end/applyVacation',this.form).then(resp=>{
                            if (resp.data==true){
                                this.editVisible=false;
                                this.$message.success("修改成功")
                                this.getData()
                            }else{
                                this.$message.error("修改失败")
                            }
                        })
                    }else {
                        this.$message.error("修改失败")
                    }
                })
            }else {
                this.postRequest('oasys-back-end/applyVacation',this.form).then(resp=>{
                    if (resp.data==true){
                        this.editVisible=false;
                        this.$message.success("修改成功")
                        this.getData()
                    }
                })
            }
        },
        // 分页导航
        handlePageChange(currentPage) {
            this.page = currentPage;
            this.getData();
        },
        sizeChange(currentSize) {
            this.size = currentSize;
            this.getData();
        },
    }
}
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 300px;
    display: inline-block;
}

.table {
    width: 100%;
    font-size: 14px;
}

.red {
    color: #ff0000;
}

.mr10 {
    margin-right: 10px;
}

.table-td-thumb {
    display: block;
    margin: auto;
    width: 40px;
    height: 40px;
}
</style>



