<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 基础表格
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-input v-model="keyword" placeholder="请输入关键字" clearable prefix-icon="el-icon-search"
                          class="handle-input"></el-input>
                <el-button type="primary" @click="getData()">搜索</el-button>
            </div>
            <el-table
                :data="tableData"
                border
                class="table"
                ref="multipleTable"
                header-cell-class-name="table-header"
                @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column label="工号" align="center">
                    <template slot-scope="scope">{{ scope.row.workId }}</template>
                </el-table-column>
                <el-table-column label="全名" align="center">
                    <template slot-scope="scope">{{scope.row.employee.fullname}}</template>
                </el-table-column>
                <el-table-column label="请假类型" align="center">
                    <template slot-scope="scope">{{ scope.row.vacationType }}</template>
                </el-table-column>
                <el-table-column label="起始时间" width="200" align="center">
                    <template slot-scope="scope">{{scope.row.startTime}}</template>
                </el-table-column>
                <el-table-column label="截止时间" width="200" align="center">
                    <template slot-scope="scope">{{scope.row.endTime}}</template>
                </el-table-column>
                <el-table-column label="状态" align="center">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.status==='通过'?'success':(scope.row.status==='未通过'?'danger':'')"
                        >{{ scope.row.status }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button type="text" icon="el-icon-edit" v-if="scope.row.status==='未审核'"
                                   @click="handleEdit(scope.$index,scope.row)">
                            修改假单
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="handleDetail(scope.$index, scope.row)"
                            v-if="scope.row.status==='通过'"
                        >查看假单
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    background
                    layout="prev, pager, next ,->, total"
                    :page-count="pageTotal"
                    :current-page="query.pageIndex"
                    :page-size="query.size"
                    @current-change="handlePageChange"
                    @size-change="sizeChange"
                    @prev-click="handlePageChange"
                    @next-click="handlePageChange"
                ></el-pagination>
            </div>
        </div>

        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="detailVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="工号">
                    <el-input v-model="form.vacation.workId" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="全名">
                    <el-input v-model="form.vacation.employee.fullname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="假期类型">
                    <el-input v-model="form.vacation.vacationType" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="假期起始时间">
                    <el-date-picker type="datetime" :disabled="true" v-model="form.vacation.startTime"></el-date-picker>
                </el-form-item>
                <el-form-item label="假期结束时间">
                    <el-date-picker type="datetime" :disabled="true" v-model="form.vacation.endTime"></el-date-picker>
                </el-form-item>
                <el-form-item label="原因">
                    <el-input type="textarea" v-model="form.vacation.reason" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="审核状态">
                    <el-input v-model="form.vacation.status" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="审核意见">
                    <el-input type="textarea" v-model="form.vacation.check.checkReason" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="假期状态">
                    <el-tag
                        :type="form.status==='放假中'?'success':(form.status==='销假'?'danger':'')"
                    >{{ form.status }}
                    </el-tag>
                </el-form-item>
                <el-form-item label="销假时间">
                    <el-input v-model="form.reportTime" :disabled="true"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="detailVisible = false">取 消</el-button>
                <el-button type="primary" @click="editReport">销 假</el-button>
            </span>
        </el-dialog>
        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
            <el-form ref="form" :model="vacation" label-width="70px">
                <el-form-item label="假单号">
                    <el-input v-model="vacation.vacationId" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="工号">
                    <el-input v-model="vacation.workId" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="全名">
                    <el-input v-model="vacation.employee.fullname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="假期类型">
                    <el-input v-model="vacation.vacationType"></el-input>
                </el-form-item>
                <el-form-item label="起始时间">
                    <el-date-picker type="date" v-model="vacation.startTime"></el-date-picker>
                </el-form-item>
                <el-form-item label="截止时间">
                    <el-date-picker type="date" v-model="vacation.endTime"></el-date-picker>
                </el-form-item>
                <el-form-item label="原因">
                    <el-input type="textarea" v-model="vacation.reason"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="detailVisible = false">取 消</el-button>
                <el-button type="primary" @click="editVacation">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {getRequest} from "../../util/api";

export default {
    name: "vacationList",
    data() {
        return {
            query: {
                address: '',
                name: '',
                pageIndex: 1,
                size: 0
            },
            keyword: null,
            tableData: [],
            multipleSelection: [],
            delList: [],
            detailVisible: false,
            editVisible: false,
            addVisible: false,
            pageTotal: 0,
            total: 0,
            size: 5,
            page: 1,
            form: {
                vacationId: "",
                status: "",
                reportTime: "",
                vacation: {
                    workId: "",
                    office: "",
                    vacationType: "",
                    startTime: "",
                    endTime: "",
                    reason: "",
                    status: "",
                    checkId: "",
                    check: {
                        checkReason: ""
                    },
                    employee: {
                        fullname: "",
                    }
                }
            },
            // vacation : {
            //     vacationId: ""
            // },
            vacation: {
                vacationId: "",
                workId: "",
                office: "",
                vacationType: "",
                startTime: "",
                endTime: "",
                reason: "",
                status: "",
                checkId: "",
                check: {
                    checkReason: ""
                },
                employee: {
                    fullname: "",
                }
            },
            idx: -1,
            id: -1,
            value: '',
            loading: false
        };
    },
    mounted() {
        this.getData();
    },
    methods: {
        // 获取 easy-mock 的模拟数据
        getData() {
            let url = 'oasys-back-end/getAllVacation?page=' + this.page + '&size=' + this.size + '&workId=' + this.$store.state.user.workId
            if (this.keyword != null) {
                console.log(this.keyword)
                url += '&keyword=' + this.keyword
            }
            getRequest(url).then(resp => {
                this.tableData = resp.data.data;
                console.log(this.tableData)
                this.total = resp.data.total;
                this.pageTotal = Math.ceil(this.total / this.size)
            })
        },
        // 多选操作
        handleSelectionChange(val) {
            this.multipleSelection = val;
            //console.log(this.multipleSelection.form)
        },
        delAllSelection() {
            const length = this.multipleSelection.length;
            let str = '';
            this.delList = this.delList.concat(this.multipleSelection);
            for (let i = 0; i < length; i++) {
                console.log(this.delList[i].workId)
                this.deleteRequest('oasys-back-end/deleteUser', this.delList[i]).then(resp => {
                    if (resp) {
                        str += this.delList[i].workId + ' ';
                    }
                })
                this.$message.error(`删除了` + str);
                this.multipleSelection = [];
                this.getData()
            }


        },
        // 查看假单操作
        handleDetail(index, row) {
            this.idx = index;
            this.vacation = row;
            let url = 'oasys-back-end/getReport?vacationId='+this.vacation.vacationId
            this.getRequest(url).then(resp=>{
                if(resp){
                    this.form=resp.data
                    console.log(this.form.status)
                }
            })
            this.detailVisible = true;
        },
        // 保存编辑
        editReport() {
            this.detailVisible = false;
            this.$message.success(`修改第 ${this.idx + 1} 行成功`);
            this.postRequest('oasys-back-end/editReport', this.form).then(resp => {
                if (resp) {
                    this.$message("修改成功");
                    this.getData();
                } else {
                    this.$message("修改失败");
                    this.getData();
                }
            })
        },
        // 新增操作
        handleAdd() {
            this.addVisible = true;
        },
        // 保存新增
        saveAdd() {
            this.addVisible = false;
            this.form.employee.workId = this.form.workId;
            console.log(this.form.employee.workId)
            this.form.employee.fullname = this.form.fullname;
            this.postRequest('oasys-back-end/addUser', this.form).then(resp => {
                if (resp) {
                    this.$message("新增成功");
                    this.getData();
                } else {
                    this.$message("新增失败");
                    this.getData();
                }
            })

        },
        handleEdit(index, row) {
            this.idx = index;
            this.vacation = row;
            this.editVisible = true;
        },
        editVacation: function () {
            this.loading = true
            this.postRequest("oasys-back-end/editVacation",this.vacation).then(resp=>{
                console.log(resp.data)
                if (resp.data===true){
                    this.loading=false
                    this.$message.success("修改成功")
                    this.editVisible=false
                    this.getData()
                }else {
                    this.loading=false
                    this.$message.warning("修改失败")
                    this.editVisible=false
                    this.getData()
                }
            })
        },
        // 分页导航
        handlePageChange(currentPage) {
            this.page = currentPage;
            this.getData();
        },
        sizeChange(currentSize) {
            this.size = currentSize;
            this.getData();
        },
    }
}
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 300px;
    display: inline-block;
}

.table {
    width: 100%;
    font-size: 14px;
}

.red {
    color: #ff0000;
}

.mr10 {
    margin-right: 10px;
}

.table-td-thumb {
    display: block;
    margin: auto;
    width: 40px;
    height: 40px;
}
</style>



